import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid';
import ScrollToTopOnMount from './ScrollTopOnMount'
import Typography from '@material-ui/core/Typography';
import Fade from '@material-ui/core/Fade';
import {makeStyles} from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import FastfoodIcon from '@material-ui/icons/Fastfood';
import LaptopMacIcon from '@material-ui/icons/LaptopMac';
import HotelIcon from '@material-ui/icons/Hotel';
import RepeatIcon from '@material-ui/icons/Repeat';
import Paper from '@material-ui/core/Paper';

const section_padding = '10vw';
const useStyles = theme => ({
    root: {
        display: 'flex',
        padding: 0,
        maxWidth: '100vw',
    },
    content: {
        // [theme.breakpoints.up('sm')]: {
        //     paddingLeft: '10vw',
        //     paddingRight: '10vw',
        // },
    },
    profile: {
        padding: `5vh ${section_padding}`,
        [theme.breakpoints.up('sm')]: {
            paddingLeft: section_padding,
            paddingRight: section_padding,
        },
        borderBottom: `3px solid ${theme.palette.divider}`,
        // backgroundImage: 'url(https://stellarium.org/img/slideshow/slide-1.jpg)',
        backgroundColor: theme.palette.background.paper,
        // color: 'white',
    },
    experience: {
        padding: `5vh ${section_padding}`,
        [theme.breakpoints.up('sm')]: {
            paddingLeft: section_padding,
            paddingRight: section_padding,
        },
        borderBottom: `3px solid ${theme.palette.divider}`,
    },
    project: {
        padding: `5vh ${section_padding}`,
        [theme.breakpoints.up('sm')]: {
            paddingLeft: section_padding,
            paddingRight: section_padding,
        },
    }
});


class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
    }


    render() {
        const {classes} = this.props;

        return (
            <Container className={classes.root}>
                <ScrollToTopOnMount/>
                <Grid
                    className={classes.content}
                    container
                    spacing={0}
                    direction="row"
                    alignItems="flex-start"
                    justify="center"
                >
                    <Grid className={classes.profile} container item xs={12} justify="flex-start">
                        <Profile/>
                    </Grid>
                    <Grid className={classes.experience} container item xs={12} justify="flex-start">
                        <Experience/>
                    </Grid>
                    <Grid className={classes.project} container item xs={12} justify="flex-start">
                        <Project/>
                    </Grid>
                </Grid>

            </Container>
        );
    }
}

const profileStyle = makeStyles((theme) => ({
    left: {
        // borderRight: `1px solid ${theme.palette.divider}`,
        // height: '100%'
        // padding: '24px 0',
        textAlign: 'center',
    },
    right: {
        // padding: '24px 0',
        textAlign: 'left',
        // borderBottom: `1px solid ${theme.palette.divider}`,
        // height: '100%'
    },
    avatar: {
        // padding: header_padding,
        margin: theme.spacing(5),
        width: theme.spacing(15),
        height: theme.spacing(15),
    },
}));

function Profile(props) {
    const classes = profileStyle();

    return (
        <Fade in={true} timeout={3000}>
            <Grid
                container
                spacing={0}
                alignItems="flex-start"
                justify="center"
            >
                <Grid className={classes.left} container item xs={12} md={3} justify="center">
                    {/*<Avatar className={classes.avatar} alt="Yi-Ruei, Lu" src={process.env.PUBLIC_URL + '/code.ico'}/>*/}
                    <Avatar className={classes.avatar}/>
                </Grid>
                <Grid className={classes.right} container item xs={12} md={9} justify="flex-start">
                    <Grid container item xs={12} justify="flex-start" alignItems="flex-start">
                        <Typography gutterBottom variant='h5'>About Me</Typography>
                        <Typography gutterBottom variant='subtitle1'>I am a software engineer in Institute for
                            Information Industry ( III ) and I am responsible for developing web applications and
                            researching the latest deep learning algorithms.</Typography>
                    </Grid>
                    <Grid container item xs={12} justify="flex-start" alignItems="flex-start">
                        <Typography gutterBottom variant='h5'>Skills</Typography>
                        <Typography gutterBottom variant='subtitle1'>Skilled in Python and JavaScript, Hands-on
                            experience in Django and Flask, Familiar with Deep Learning algorithms, Hands-on experience
                            in Tensorflow and Keras, Strong background knowledge of Computer Science</Typography>
                    </Grid>
                </Grid>

            </Grid>
        </Fade>
    );
}

const experienceStyles = makeStyles((theme) => ({
    paper: {
        padding: '6px 16px',
    },
    secondaryTail: {
        backgroundColor: theme.palette.secondary.main,
    },
}));

function Experience() {
    const classes = experienceStyles();

    return (
        <Timeline align="alternate">
            <TimelineItem>
                <TimelineOppositeContent>
                    <Typography variant="body2" color="textSecondary">
                        9:30 am
                    </Typography>
                </TimelineOppositeContent>
                <TimelineSeparator>
                    <TimelineDot>
                        <FastfoodIcon/>
                    </TimelineDot>
                    <TimelineConnector/>
                </TimelineSeparator>
                <TimelineContent>
                    <Paper elevation={3} className={classes.paper}>
                        <Typography variant="h6" component="h1">
                            Eat
                        </Typography>
                        <Typography>Because you need strength</Typography>
                    </Paper>
                </TimelineContent>
            </TimelineItem>
            <TimelineItem>
                <TimelineOppositeContent>
                    <Typography variant="body2" color="textSecondary">
                        10:00 am
                    </Typography>
                </TimelineOppositeContent>
                <TimelineSeparator>
                    <TimelineDot color="primary">
                        <LaptopMacIcon/>
                    </TimelineDot>
                    <TimelineConnector/>
                </TimelineSeparator>
                <TimelineContent>
                    <Paper elevation={3} className={classes.paper}>
                        <Typography variant="h6" component="h1">
                            Code
                        </Typography>
                        <Typography>Because it&apos;s awesome!</Typography>
                    </Paper>
                </TimelineContent>
            </TimelineItem>
            <TimelineItem>
                <TimelineSeparator>
                    <TimelineDot color="primary" variant="outlined">
                        <HotelIcon/>
                    </TimelineDot>
                    <TimelineConnector className={classes.secondaryTail}/>
                </TimelineSeparator>
                <TimelineContent>
                    <Paper elevation={3} className={classes.paper}>
                        <Typography variant="h6" component="h1">
                            Sleep
                        </Typography>
                        <Typography>Because you need rest</Typography>
                    </Paper>
                </TimelineContent>
            </TimelineItem>
            <TimelineItem>
                <TimelineSeparator>
                    <TimelineDot color="secondary">
                        <RepeatIcon/>
                    </TimelineDot>
                </TimelineSeparator>
                <TimelineContent>
                    <Paper elevation={3} className={classes.paper}>
                        <Typography variant="h6" component="h1">
                            Repeat
                        </Typography>
                        <Typography>Because this is the life you love!</Typography>
                    </Paper>
                </TimelineContent>
            </TimelineItem>
        </Timeline>
    );
}

const cardStyle = makeStyles({
    left: {
        // borderRight: `1px solid ${theme.palette.divider}`,
        // height: '100%'
        // padding: '24px 0',
        textAlign: 'center',
    },
    right: {
        // padding: '24px 0',
        textAlign: 'left',
        // borderBottom: `1px solid ${theme.palette.divider}`,
        // height: '100%'
    },
    root: {
        minHeight: 400,
        maxWidth: 400
        // maxWidth: 345,
        // maxHeight: 300,
    },
    card_area: {
        // display: 'flex',
        // justifyContent: 'bottom',
    },
    overlay_text: {
        padding: '12px 24px',
        position: 'absolute',
        bottom: 0,
        left: 0,
        color: 'white',
        backgroundColor: 'rgba(0,0,0,0.5)'
    },
    card_media: {
        minHeight: 300,
        maxWidth: 400
        // height: 320,
        // width: 480,
    }
});

const pojects_info = [
    {
        image: 'https://cdn-images-1.medium.com/max/500/1*q1uVc-MU-tC-WwFp2yXJow.gif',
        title: 'Real-time Object Detection',
    },
    {
        image: 'https://thumbs.gfycat.com/DimSarcasticCockerspaniel-size_restricted.gif',
        title: 'Segmentation on Traffic',
    },
    {
        image: 'https://1.bp.blogspot.com/-H71UdvAObkQ/XVrSxUc2rFI/AAAAAAAAEhU/h7NuEZ23Pu4XAdwCcaKNxakGbN4nJUc2wCLcBGAs/s400/image2.gif',
        title: 'Hand Tracking and Gesture Recognition',
    },
    {
        image: 'https://thumbs.gfycat.com/CoordinatedDentalIggypops-max-1mb.gif',
        title: 'Stock Price Prediction',
    }
];


function Project(props) {
    const classes = cardStyle();
    let ProjectCards = [];
    for (let i = 0; i < pojects_info.length; i++) {
        ProjectCards.push(
            <Grid key={i} className={classes.left} container item xs={12} md={6} justify="center">
                <Fade in={true} timeout={3000}>
                    <Card className={classes.root} raised={true}>
                        <CardActionArea className={classes.card_area}>
                            <CardMedia
                                className={classes.card_media}
                                component="img"
                                image={pojects_info[i].image}
                            />
                        </CardActionArea>
                        <CardContent>
                            <Typography variant="h5" component="h2">
                                {pojects_info[i].title}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Here is some description of the project.
                            </Typography>
                        </CardContent>
                    </Card>
                </Fade>
            </Grid>
        )
    }
    return (
        <Grid
            container
            spacing={5}
            alignItems="flex-start"
            justify="center"
        >
            {ProjectCards}
        </Grid>
    );
}

export default withStyles(useStyles)(About);
